* {
    box-sizing: border-box;
}

body {
    margin: 0;
    height: 90vh;
    background-color: #0B2434;
    padding: 20px;
    font-family: 'Karla', sans-serif;
    display: flex;
    flex-direction: column; 
    justify-content:center;
}

main {
    margin: 0px;
    background-color: #F5F5F5;
    border-radius: 5px;
    padding: 20px;
    padding-top: 5px;
    display: flex;
    flex-direction: column;
    justify-content:space-around;
    align-items: center;
}

.title {
    font-size: 40px;
    margin: 5px;
    margin-top: 15px;
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 5px;
    /* text-align: center; */
    line-height: 1.5em;
    font-size: 0.9rem;
}

.dice-container {
    margin: 0;
    padding: 0;
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
}

.die-face {
    margin: 15px;
    height: 50px;
    width: 50px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    /* flex-wrap: wrap; */
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.die-num {
    /* border: #0B2434 solid 1px; */
    font-size: 2rem;
}

.roll-dice {
    margin-top: 25px;
    height: 50px;
    width: 250px;
    border: none;
    border-radius: 6px;
    background-color: #5035ff;
    color: white;
    font-size: 1.2rem;
    font-family: 'Karla', sans-serif;
    cursor: pointer;
}

.roll-dice:focus {
    outline: none;
}

.roll-dice:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.roll-dice-inactive {
    margin-top: 25px;
    height: 50px;
    width: 250px;
    border: none;
    border-radius: 6px;
    color: white;
    font-size: 1.2rem;
    font-family: 'Karla', sans-serif;
    background-color: #d4d0ee;
    cursor: none;
}
.header{
    /* border: red solid 1px; */
    margin: 0;
    padding: 0;
    width: 95%;
    display: flex;
    justify-content: center;
}
.timer-group{
    margin: 20px 5px;
    padding: 0px 15px 15px 15px;
    display: flex;
    flex-direction: column;
    justify-content:flex-end;
    border-radius: 50%;
    color: #93919d;
    box-shadow: 5px 5px 30px 3px rgba(175, 164, 246, 0.2);
}
.timer{
    width: 40px;
    margin: 0;
    padding: 0;
    font-size: 1.2rem;
    letter-spacing: 2.5px;
    font-weight: 700;
    text-align: center;
}
